<template>
  <ul class="chat-list">
    <li v-for="article in articles" :key="article.articleId" @click="route(article.articleId)">
      <h3 class="chat-title">{{article.title}}</h3>
      <p class="chat-desc">{{article.desc}}</p>
      <p class="chat-author">{{article.author}}</p>
    </li>
  </ul>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'list',

    data () {
      return {
        articles: []
      }
    },

    mounted () {
      this.getArticles()
    },

    methods: {
      getArticles () {
        let vm = this

        axios.get('http://m.xihu.fm/api/xihu/stories')
          .then(function (response) {
            // console.log(response)
            vm.articles = response.data.articles
          })
          .catch(function (error) {
            console.log(error)
          })
      },
      route (id) {
        this.$router.push({
          name: 'Detail',
          query: {
            id: id
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import './list.scss';
</style>




