<template>
  <div class="chat-detail">
    <div class="chat-info">
      <h2 class="chat-title">{{article.title}}</h2>
      <p class="chat-desc">{{article.desc}}</p>
      <p class="chat-author">{{article.author}}</p>
    </div>
    <div class="chat-list">
      <div class="chat-list-item" v-for="item in chats" :key="item.id" :id="item.id">
        <div class="chat-list-row" :class="item.type">
          <div class="chat-avatar" v-if="item.type == 'left'">
            <span>{{item.name.substr(0,1)}}</span>
            <p>{{item.name}}</p>
          </div>
          <p class="chat-list-content">{{item.content}}</p>
          <div class="chat-avatar" v-if="item.type == 'right'">
            <span>{{item.name.substr(0,1)}}</span>
            <p>{{item.name}}</p>
          </div>
        </div>
        <div class="chat-item-reply" :class="item.type" v-if="item.bullets.length > 0">
          <p v-for="(reply, i) in item.bullets" :key="i">{{reply.name}}：{{reply.comment}}</p>
        </div>
      </div>
    </div>
    <div class="chat-footer">
      <button @click="dialog(1)">评 论</button>
      <button @click="play">继 续</button>
      <button @click="autoPlay">自动围观</button>
    </div>
    <div class="chat-dialog" id="chat-dialog">
      <div class="chat-comment-form">
        <h3>评论</h3>
        <div class="chat-cf-item">
          <input type="text" v-model="uname" placeholder="昵称" :maxlength="limitForName">
        </div>
        <div class="chat-cf-item">
          <textarea class="chat-textarea" v-model="comment" placeholder="说点什么" :maxlength="limitForComment"></textarea>
        </div>
        <div class="chat-cf-item">
          <button class="chat-publish" @click="publish">发表</button>
        </div>
      </div>
      <a href="javascript:;" class="chat-close" @click="dialog(2)">X</a>
    </div>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  name: 'detail',

  props: {
    during: {
      type: Number,
      default: 1000
    },
    limitForName: {
      type: Number,
      default: 10
    },
    limitForComment: {
      type: Number,
      default: 144
    }
  },

  data() {
    return {
      article: '',
      chats: [],
      uname: '',
      comment: ''
    }
  },

  mounted() {
    this.getArticleInfo()
  },

  methods: {
    getArticleInfo() {
      let vm = this

      axios.get('http://m.xihu.fm/api/xihu/stories/' + vm.$route.query.id)
        .then(function(response) {
          console.log(response)
          vm.article = response.data

          document.querySelector('.chat-list').innerHTML = ''

          vm.setChatData()
        })
    },
    setChatData() {
      let vm = this

      let chatsData = vm.article.chats

      if (!chatsData.length) {
        vm.autoplay && clearInterval(vm.autoplay)
        return
      }

      let chatObj = chatsData.shift()

      vm.chatId = chatObj.id
      if (chatObj.content.length < 200) {
        vm.during.default = 1000
      } else if (chatObj.content.length > 200) {
        vm.during.default = 2000
      }
      vm.chats.push(chatObj)
    },
    play() {
      this.setChatData()
    },
    autoPlay() {
      let vm = this

      let autoplay = setInterval(function() {
        vm.play()
      }, vm.during)

      vm.autoplay = autoplay
    },
    dialog(flag) {
      let display = flag === 1 ? 'block' : 'none'
      document.querySelector('#chat-dialog').style.display = display
    },
    publish() {
      let vm = this

      if (!vm.uname || !vm.comment) {
        return
      }

      var publishUrl = 'http://m.xihu.fm/api/xihu/stories/{chat}/bullet'
      publishUrl = publishUrl.replace(/{chat}/g, vm.chatId)

      axios.post(publishUrl, {
          name: vm.uname,
          content: vm.comment
        })
        .then(function(response) {
          if (response.data.code === 0) {
            vm.setBullet()
          }
        })
    },
    setBullet() {
      let vm = this

      let bullet = {}
      bullet.cname = vm.uname
      bullet.memo = vm.comment

      let chats = vm.chats

      chats.forEach(function(item) {
        if (item.id === vm.chatId) {
          item.bullets.push(bullet)
        }
      })

      vm.chats = chats

      vm.resetForm()
    },
    resetForm() {
      let vm = this
      vm.uname = ''
      vm.comment = ''

      vm.dialog(2)
    }
  }
}

</script>
<style lang="scss">
@import './detail'

</style>



